import React from "react";
import Slider from "react-slick";
import { getposters, getfilmdata, updatecount, getnext, getlast} from '../actions/card'
import { connect } from 'react-redux'

class SimpleSlider extends React.Component {
  componentDidMount(){
    this.props.getposters()
    this.props.getfilmdata()
  }
  next = () => {
    if (this.props.count < 4) {
      this.props.updatecount(this.props.count + 1)
    }
    if (this.props.count === 4) {
      this.props.updatecount(0)
      this.props.getnext(this.props.trigger, this.props.posters, this.props.numbs)
    }
  }
  last = () => {
    if (this.props.count > -4) {
      this.props.updatecount(this.props.count - 1)
    }
    if (this.props.count === -4) {
      this.props.updatecount(0)
      this.props.getlast(this.props.trigger, this.props.posters, this.props.numbs)
    }
  }
  imageClick = (n) => {
    this.props.getfilmdata(n)
  }
  render() {
    const { posters } = this.props
    const {numbs} = this.props
    let course = 0
    function SampleNextArrow(props) {
      const { className, style, onClick } = props;
      return (
        <img
          className={className}
          style={{ ...style, display: "block", width: "40px", height: "40px", right: "5px", zIndex:"1"}}
          onClick={onClick}
          src={"butt/up.png"}
          alt="up"
        />
      );
    }
    
    function SamplePrevArrow(props) {
      const { className, style, onClick } = props;
      return (
        <img
          className={className}
          style={{ ...style, display: "block", width: "40px", height: "40px", left: "5px", zIndex:"1"}}
          onClick={onClick}
          src={"butt/dn.png"}
          alt="dn"
        />
      );
    }
    var settings = {
      infinite: true,
      slidesToShow: 5,
      vertical: true,
      beforeChange: (current, next) => {
		    if (next === 0 && current === 14)  { next = 15 }
		    if (next === 14 && current === 0)  { current = 15 }
        course = next - current
	    },
      afterChange: () => {
        if (course === 1) {
          this.next()
        }
		    if (course === -1) {
          this.last()
		    }
	    },
      slidesToScroll: 1,
      nextArrow: <SampleNextArrow />,
      prevArrow: <SamplePrevArrow />
    };
    return (
      <div style={{
            width: "200px"
          }}>
        <Slider {...settings}>
        {posters.map(poster => (
          <div id ="posters">
            <input 
              type="hidden" 
              name={'id' + poster[0]} 
              value={poster[0]}
            />
            <div style={{ color: "white"} }>{poster[0]}</div>
            <img
              className="image"
              src={'imgs/' + poster[1] +  'k0.jpg'}
              alt={numbs}
              onClick={() => this.imageClick(poster[0])}
            />
          </div>
        ))}
        </Slider>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  numbs :  state.post.numbs,
  posters: state.post.posters,
  count: state.post.count,
  trigger: state.post.trigger
})
export default connect(mapStateToProps, { getposters, getfilmdata, updatecount, getnext, getlast })(SimpleSlider)
